:root {
  --light: #F5F5F5;
  --light-light: #f0f0f0;
  --light-dark:#F2F2F2;
  --dark: #231F20;
  --muted: #424242;
  --muted-light: #7e7e7e;
  --muted-light-light: #b4b3b3;
  --body-color: #231F20;
  --body-bg: #F5F5F5;
  --overlay:rgba(58, 58, 58, 0.2);
  --primary: #a79600;
  --primary-dark: #162355;
  --primary-light: #7e95f0;
  --primary-light: #b1b6ce;
  --secondary: #c5e062;
  --success: #61e682;
  --accent: #e0dd21;
}

:root {
  --doc-height: 100%;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  width: 100%;
  height: 100vh; /* fallback for Js load */
  height: var(--doc-height);
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-content: center;
  height: 100vh; /* fallback for Js load */
  height: var(--doc-height);
}

a {
  text-decoration: underline;
  color: var(--primary);
}
li {
  padding: 0.2rem;
}

.wordpress-page-area *:not(li) {
  margin-bottom: 1.7rem;
}

p {
  font-family: "Open Sans", sans-serif;
  text-align: justify;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Open Sans", sans-serif;
  color: var(--primary);
}

#logo1-text #logo-heading {
  color: white !important;
}

#logo1-text #logo-heading {
  color: white !important;
}

[class^=Display] {
  font-family: "Open Sans", sans-serif;
}

#welcome #welcome-display-text {
  color: white !important;
}

.section-title {
  color: var(--primary);
  font-weight: 400;
}

.section-link {
  font-size: 0.8rem;
  color: var(--dark);
}
.section-link:hover {
  color: var(--primary-light);
}

.news-tags {
  color: var(--secondary);
}

.news-title {
  color: var(--dark);
}

.news-date {
  color: var(--muted);
}

.calendar-date {
  color: var(--secondary);
}

.calendar-month {
  color: var(--dark);
}

.calendar-event {
  color: var(--primary);
}

.calendar-time {
  color: var(--muted);
}

.content-area h1, .content-area h2, .content-area h3, .content-area h4, .content-area h5, .content-area h6,
.content-area .h1, .content-area .h2, .content-area .h3, .content-area .h4, .content-area .h5, .content-area .h6 {
  color: var(--primary);
  font-weight: 500 !important;
}
.content-area h1 strong, .content-area h2 strong, .content-area h3 strong, .content-area h4 strong, .content-area h5 strong, .content-area h6 strong,
.content-area .h1 strong, .content-area .h2 strong, .content-area .h3 strong, .content-area .h4 strong, .content-area .h5 strong, .content-area .h6 strong {
  font-weight: 500;
}

.is-style-wlt-file-block a,
.editor-styles-wrapper .is-style-wlt-file-block a {
  background-color: var(--primary-light-light);
  padding: 1rem;
  margin-bottom: 4px;
  color: var(--dark);
  font-weight: 500;
  text-decoration: none;
  display: flex;
  align-items: center;
}
.is-style-wlt-file-block a:hover,
.editor-styles-wrapper .is-style-wlt-file-block a:hover {
  background-color: var(--primary);
  color: white;
}
.is-style-wlt-file-block a:hover::before,
.editor-styles-wrapper .is-style-wlt-file-block a:hover::before {
  color: white;
}
.is-style-wlt-file-block a::before,
.editor-styles-wrapper .is-style-wlt-file-block a::before {
  font-family: "bootstrap-icons";
  content: "\f369";
  font-size: 1rem;
  color: var(--muted);
  padding-right: 1rem;
}

.is-style-wlt-quote-block,
.editor-styles-wrapper .is-style-wlt-quote-block {
  margin-top: 1.8rem;
  margin-bottom: 1.8rem;
  text-align: center;
  padding-left: 4rem;
  padding-right: 4rem;
}
.is-style-wlt-quote-block p,
.editor-styles-wrapper .is-style-wlt-quote-block p {
  font-family: "Cormorant", serif;
  font-weight: 300 !important;
}

.custom #my-search {
  float: right;
  width: 306px;
}

.custom form.search_form_visible {
  width: 306px;
}

.custom form.search_form_visible #s {
  background: #fff;
  border: 1px solid #000;
  font-size: 12px;
  margin: 0 6px 0 0;
  padding: 4px;
  width: 215px;
  float: left;
}

.custom #my-search input#searchsubmit.my-search {
  background: #000;
  color: #fff;
  cursor: pointer;
  float: right;
  padding: 4px;
}

.wlt-latest-posts img {
  margin-bottom: 0.5rem;
}
.wlt-latest-posts li {
  border: var(--light) 1rem solid;
  box-shadow: 5px 10px 18px #888888;
  padding: 0;
  min-height: fit-content;
}
.wlt-latest-posts a.wp-block-latest-posts__post-title {
  color: var(--primary);
  font-weight: bold;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}
.wlt-latest-posts a.wp-block-latest-posts__post-title:visited {
  color: var(--primary);
}
.wlt-latest-posts a.wp-block-latest-posts__post-title, .wlt-latest-posts time, .wlt-latest-posts div.wp-block-latest-posts__post-excerpt {
  padding-left: 0.8rem;
  padding-right: 0.8rem;
}

.is-style-wlt-custom-button a,
.editor-styles-wrapper .is-style-wlt-custom-button a {
  background-color: var(--primary) !important;
  padding: 0.7rem 1.3rem !important;
  font-weight: 500 !important;
  font-size: 1rem;
  color: white !important;
  border-radius: 0 !important;
  min-width: 3rem;
  border: none !important;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
.is-style-wlt-custom-button a:hover,
.editor-styles-wrapper .is-style-wlt-custom-button a:hover {
  background-color: var(--accent) !important;
  border: none !important;
  color: white !important;
}

.is-style-wlt-custom-external-file-link a,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a {
  background-color: var(--primary-light);
  padding: 1rem;
  margin-bottom: 4px;
  color: var(--muted);
  font-weight: 600;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
}
.is-style-wlt-custom-external-file-link a:hover,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a:hover {
  background-color: var(--primary);
  color: white;
}
.is-style-wlt-custom-external-file-link a:hover::before,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a:hover::before {
  color: white;
}
.is-style-wlt-custom-external-file-link a::before,
.editor-styles-wrapper .is-style-wlt-custom-external-file-link a::before {
  font-family: "bootstrap-icons";
  content: "\f369";
  font-size: 1rem;
  color: var(--muted);
  padding-right: 1rem;
}

.is-style-wlt-custom-button a,
.editor-styles-wrapper .is-style-wlt-custom-button a {
  background-color: var(--accent) !important;
  padding: 0.7rem 1rem !important;
  font-weight: 500 !important;
  font-size: 1rem;
  color: var(--primary) !important;
  border-radius: 0 !important;
  min-width: 3rem;
  border: none !important;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
.is-style-wlt-custom-button a:hover,
.editor-styles-wrapper .is-style-wlt-custom-button a:hover {
  background-color: var(--primary) !important;
  border: none !important;
  color: white !important;
}

.aioseo-html-page-sitemap a {
  text-decoration: none;
}